progress {
  border-radius: 100px;
  border: none;
  margin: 10px 0 0 5%;
  width: 70%;
  height: 22px;
  background-color: #CE8B4D;
}

progress::-moz-progress-bar {
    background-color: #632F0C;
    border-radius: 100px;
}
.mainDiv{
    text-align: center;
    height: 350px;
}

.questionText{
    margin-top: 10%;
}

.questionTextAM{
    margin-top: 20%;
}

.questionImage{
    max-height: 200px;
    margin-top: 10%;
}

.frontCard, .secondCard, .thirdCard{
    position: absolute;
    border-radius: 20px;
    left: 50%;
    transform: translateX(-50%);
}

.frontCard{
    text-align: center;
    width: 50%;
    height: 350px;
    background-color: #FFFDF0;
}

.secondCard{
    width: 40%;
    height: 360px;
    background-color: #E9E7DC;
}
.thirdCard{
    width: 30%;
    height: 370px;
    background-color: #D2D0C4;
}

.column {
    float: left;
    width: 50%;
}

.row:after {
    content: "";
    display: table;
    clear: both;
}