.card11 {
    text-align: center;
    padding: 20px;
    margin: 150px auto 0 auto;
    width: 50%;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0px 10px #D2D0C4;
}

.cardTitle{
    position: relative;
    margin: 15px auto;
    font-size: 250%;
}

.cardImage{
    margin: 30px 20px;
    position: relative;
    max-width: 50%;
    float:none;
}

.cardImage::after{
    content: "";
    display: table;
    clear: both;
}