.answerBtn, .correct_answer, .incorrect_answer{
    margin: 75px 2% 0 2%;
    padding: 10px;
    width: 20%;
    font-size: 30px;
    border-radius: 100px;
    border: none;
}

.answerBtn{
    background-color: white;
    box-shadow: 0px 10px #D2D0C4;
}

.correct_answer{
    background-color: green;
    box-shadow: 0px 10px #004400;
    color: white;
}

.incorrect_answer{
    background-color: red;
    box-shadow: 0px 10px #AA0000;
    color: white;
}

.quizRow {
    text-align: center;
}

.quizRow::after {
    content: "";
    display: table;
    clear: both;
}

.loadingQuestion{
    position: absolute;
    margin-top:400px;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);

}