.CookieButton{
    margin: 75px auto 0 auto;
    width: 50%;
    border-top-right-radius: 100px;
    border-bottom-right-radius: 100px;
    background-color: white;
    box-shadow: 0px 10px #D2D0C4;
    border: none;
}

.CookieAmount{
    font-size: 35px;
    margin: 13px auto;
}

.CookieImgBtn{
    max-width: 60px;
    float: left;
    margin: 5px 0 5px 50px;
}

.OptionsText{
    font-size: 35px;
    margin: 13px 50px;
    float: left;
}

.OptionsImgBtn{
    max-width: 40px;
    margin: 15px 50px 15px 0;
    position: absolute;
    right: 0;
}

.OptionsButton{
    position: absolute;
    margin: 75px auto 0 auto;
    width: 50%;
    border-top-left-radius: 100px;
    border-bottom-left-radius: 100px;
    background-color: white;
    box-shadow: 0px 10px #D2D0C4;
    right: 0px;
    margin-right: 0;
    border: none;
}

.left-column{
    float: left;
    width: 33%;
}

.mid-column{
    float: left;
    width: 33%;
}

.right-column{
    position: relative;
    float: left;
    width: 34%;
}