.btnCategories, .btnMenu, .btnHome{
    position: relative;
    color: white;
    background-color: rgba(26,188,156,1);
    box-shadow: 0px 10px rgba(22,160,133,1);
    width: 30%;
    height: 80px;
    font-size: 50px;
    margin: 20px auto;
    border: none;
    border-radius: 100px;
    left: 50%;
    transform: translateX(-50%);
}

.btnMenu:active, .btnHome:active{
    margin: 25px auto;
    background-color: rgba(22,160,133,1);
    box-shadow: 0 0;
}

.btnMenu, .btnCategories{
    margin: 15px auto;
    width: 70%;
    height: auto;
    font-size: 40px;
    position: auto;
    left: 0;
    transform: translateX(0);
}

.btnCategories{
    border-radius: 10px;
    width: 30%;
    margin: 15px 5%;
}

@media only screen and (max-width: 1000px) {
    .btnHome {
        width: 300px;
    }
  }